* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
.home {
	width: 100%;
	height: 100vh;
	background-color: bisque;
}
.home-content {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100%;
	h1 {
